<style lang="scss">
  #login{
    width: 100%;
    height: 100vh;
    background: url('~@/assets/images/loginBg.svg') no-repeat;
    background-color: rgb(246,246,246);
    background-size: contain;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    $width: 140px;
    .login-box{
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: calc(100% - 140px);
      height: calc(100vh - 140px);
      min-height: 400px;
      max-width: 1300px;
      margin: 0 auto;
      border-radius: 10px;
      background: white;
      box-shadow: 0 1px 20px rgba(0,0,0,.15);
      border: 1px solid rgba(0,0,0,.05);
      &-left{
        width: 70%;
        height: 100%;
        position: relative;
        .logo{
          width: 170px;
          position: absolute;
          top: 20px;
          left: 20px;
          img{
            width: 100%;
          }
        }
        .left-info{
          width: 100%;
          height: 100%;
          background: url('~@/assets/images/receptionist-job-description.jpg') no-repeat;
          background-size: contain;
          background-position: 50% 50%;
        }
      }
      &-right{
        width: 30%;
        .subtitle{
          margin-bottom: 0px;
          color: rgba(0,0,0,.5);
          font-size: 16px;
        }
        .form{
          width: 300px;
          position: relative;
          .error{
            margin: 15px 0;
            color: $red;
            font-size: 14px;
            min-height: 19px;
          }
          .input{
            margin-bottom: 15px;
          }
          button{
            width: 180px;
            margin-top: 20px;
          }
          .tips{
            font-size: 14px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: $grey;
          }
          .other{
            margin-top: 60px;
            font-size: 14px;
            color: $grey;
            &-box{
              display: flex;
              align-items: center;
              justify-content: flex-start;
              margin-top: 20px;
              &-item{
                svg{
                  width: 35px;
                  height: 35px;
                  margin-right: 35px;
                  cursor: pointer;
                }
                .wx{
                  &:hover{
                    path{
                      fill: rgb(30,208,55)
                    }
                  }
                }
                .dd{
                  &:hover{
                    path{
                      fill: rgb(51,153,255)
                    }
                  }
                }
                .wb{
                  &:hover{
                    path{
                      fill: rgb(237,50,41)
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
</style>

<template>
  <div id="login">
    <div class="login-box">
      <div class="login-box-left">
        <div class="logo">
          <img src="@/assets/images/logo.png" alt="logo">
        </div>
        <div class="left-info"></div>
      </div>
      <div class="login-box-right">
        <h1>欢迎登录</h1>
        <p class="subtitle">{{ title }}</p>
        <form class="form" @keyup.enter="login">
          <div class="error">
            <span v-if="errmsg"><i class="el-icon-warning"></i> 错误：{{ errmsg }}</span>
          </div>
          <el-input class="input" v-model="username" placeholder="请输入您的账户" autofocus></el-input>
          <el-input class="input" placeholder="请输入您的密码" v-model="password" show-password></el-input>
          <div class="tips">
            <p>没有帐号？<a @click="register">立即注册</a></p>
            <a @click="forgot">忘了密码</a>
          </div>
          <el-button type="primary" @click="login" :loading="postting">登录</el-button>
          <div class="other">
            <p>使用第三方平台登录</p>
            <div class="other-box">
              <div class="other-box-item">
                <svg
