                <svg
                  @click="loginByOther('dd')"
                  t="1590549271786"
                  class="icon dd"
                  viewBox="0 0 1024 1024"
                  version="1.1"
                  xmlns="http://www.w3.org/2000/svg"
                  p-id="19157"
